/* Services Section */
.services {
    text-align: center;
    padding: 4rem 2rem;
    overflow-x: hidden;
    background-color: #f7f7f7;
    min-height: 100vh; /* Ensures it covers the whole screen height */
}

.services h2 {
    font-size: 2.5rem;
    margin-bottom: 2rem;
    font-weight: 700; /* Bold weight */
    font-family: 'Montserrat', sans-serif; /* Modern sans-serif font */
    letter-spacing: 1px; /* Slightly increased letter spacing */
}

.services-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem;
    position: relative; /* Required for positioning buttons */
}

.services-content > * {
    margin-bottom: 1.5rem;
}

.services-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.877); /* Semi-transparent black */
    backdrop-filter: blur(10px); /* Increase blur for a glass effect */
    color: white; /* Text color */
    padding: 4rem; /* Increased padding to adjust height */
    border-radius: 12px; /* Slightly rounded corners */
    box-shadow: 0 4px 6px rgb(217, 209, 209); /* Add a slight shadow for depth */
    transition: transform 0.3s ease-in-out;
    min-height: 500px; /* Minimum height to ensure the card has sufficient height */
    position: relative; /* Required for button positioning */
}

.services-card:hover {
    transform: scale(1.05);
}

.description {
    color: #000;
    flex: 1;
    padding-right: 1.5rem; /* Increased padding */
    text-align: left;
    font-family: 'Arial', sans-serif; /* Changed to a sans-serif font */
}



.description h3 {
    color: #000;
    font-size: 2.2rem; /* Increased font size */
    margin-bottom: 1rem;
    font-weight: 600; /* Semi-bold weight */
    font-family: 'Poppins', sans-serif; /* Stylish sans-serif font */
    letter-spacing: 0.5px; /* Slightly increased letter spacing */
}

.description ul {
    color: #000;
    list-style-type:circle;
    padding-left: 1.5rem;
    
}

.description li {
    
    color: #0000009e;
    font-size: 1.15rem; /* Slightly larger text */
    line-height: 2.6;
    font-weight: 200; /* Normal weight */
    font-family: 'Roboto', sans-serif; /* Clean and readable font */
}

.image {
    flex: 1;
    text-align: right;
}

.image img {
    width: 100%;
    height: auto;
    border-radius: 12px; /* Match card radius */
    object-fit: cover;
}

/* Navigation buttons */
/* Navigation buttons */
.navigation-buttons {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    justify-content: center;
    width: calc(100% + 0rem); /* Adjust width to add spacing */
    margin-top: 50px;
    box-sizing: border-box; /* Ensure padding is included in width */
}

.nav-button {
    background-color: rgb(0, 0, 0); /* Change background to white on hover */
    color: rgba(255, 255, 255, 0.8); /* Change text color to dark on hover */

    border: none;
    padding: 1.5rem; /* Increased padding for larger button */
    border-radius: 50%; /* Circular shape */
    cursor: pointer;
    font-size: 2rem; /* Increased font size for arrow */
    transition: background-color 0.3s ease, color 0.3s ease; /* Transition for color change */
    margin: 0 1rem; /* Increased margin for more spacing */
    width: 60px; /* Increased size */
    height: 60px; /* Increased size */
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 4px 6px rgba(217, 209, 209, 0.1); /* Optional shadow for depth */
}

.nav-button:hover,
.nav-button.active {
    background-color: rgba(255, 255, 255, 0.877); /* Semi-transparent black */
    color: rgb(0, 0, 0);
}

/* Arrow icons */
.nav-button.prev::before {
    content: '\00ab'; /* Unicode for left double arrow (<<) */
    font-size: 2rem; /* Increased arrow size */
}

.nav-button.next::before {
    content: '\00bb'; /* Unicode for right double arrow (>>) */
    font-size: 2rem; /* Increased arrow size */
}

/* Pagination dots */
.pagination-dots {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
}

.dot {
    height: 12px;
    width: 12px;
    margin: 0 4px;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.3s ease;
    cursor: pointer;
}

.dot.active {
    background-color: rgba(0, 0, 0, 0.7);
}

/* Mobile-specific styles */
@media (max-width: 768px) {
    .services-card {
        flex-direction: column;
        min-height: 300px; /* Adjust for mobile view if needed */
    }

    .description {
        padding-right: 0;
        padding-bottom: 1rem;
    }

    .image {
        text-align: center;
    }

    .navigation-buttons {
        width: calc(100% + 0rem); /* Adjust width to add spacing */
        position:relative; /* Reset for mobile */
        justify-content: center;
        align-items: center;
        padding: 0;
        top: auto;
        transform: none;
        margin-top: 1.5rem;
    }
}
